@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.select__container {
    display: flex;
    position: relative;
    font-family: 'Montserrat', sans-serif;
    color: black;
    font-size: 19px;
}

.select__input {
    line-height: .9em;
    flex-basis: 100%;
    padding: .8em;
    outline: none;
    border: none;
    -webkit-transition: .5s;
    transition: .5s;
    cursor: pointer;
    background: none;
    font-size: inherit;
    color: inherit;
}

.select__list {
    position: absolute;
    display: none;
    flex-direction: column;
    margin-top: 2.7em;
    background-color: #fff;
    width: 100%;
    z-index: 1;
    overflow-y: auto;
    max-height: 40vh;
}
.select__list_display {
    display: flex;
}
.select__main-icon {
    width: 2.5em;
    height: 2.5em;
}
.input__wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    background: #fff;
    -webkit-transition: .5s;
    transition: .5s;
}

.input__wrapper:hover, .input__wrapper_focus {
    background-color: #FF908B;
}

.input__close {
    width: 2em;
    margin-right: .3em;
    cursor: pointer;
}
.input__close::after {
    content: '';
    background: #fff;
}
.list-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.list-item__icon {
    width: 2.5em;
    height: 2.5em;
    padding-right: 0.5em;
    cursor: pointer;
}

.list-item__text {
    font-size: inherit;
    padding: 1em;
    cursor: pointer;
    width: 75%;
    overflow: hidden;
}

.list-item_selected, .list-item:hover {
    background-color: #FF908B;
}

.select__label-text {
    position: absolute;
    -webkit-transition: .3s;
    transition: .3s;
    pointer-events: none;
    left: 1em;
    top: .8em;
}

.input__wrapper_focus~.select__label-text,
.select__label-text_small
{
    top: 0.1em;
    font-size: 0.6em;
    left: 0.3em;
    opacity: .5;
}

.modal {
    opacity: 0; 
    background: rgba(0,0,0,0.7); 
    pointer-events: none; 
    position: fixed; 
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    margin: 0;
    padding: 0;
}
.modal_opened {
    opacity: 1; 
    pointer-events: auto; 
    overflow-y: auto; 
}
.modal-wrapper {
    margin:auto;
    margin-top:25vh; 
    max-width: 40vw;
}

.CSS-modal-inner {
    position: relative;
    background: #fff; 
    border: 1px solid #ccc; 
    border-radius: 0px; 
}
.modal-header {
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
    background:#f1f1f1;
    position:relative;
}
.modal-title {
    font-size: 18px; 
    color:#555; 
    font-weight:bold; 
    line-height: 1.5; 
    margin-top: 0;
    margin-bottom: 0;
}

.modal-control {
    font-size: 24px; 
    color: #555; 
    text-decoration: none;
}
.modal-headtext {
    font-size: 18px; 
    color: #555; 
    line-height: 30px;
}
.modal-control:hover {
    color: #000; 
    cursor: pointer;
}
.modal-open {
    padding: .8em;
    border: none;
    cursor: pointer;
    background: #fff;
    font-size: inherit;
    font-family: Montserrat;
    border-radius: 3px;
    -webkit-transition: .3s;
    transition: .3s;
}

.modal-open:hover {
    background-color: #f7907d;
}

.modal-content {
    padding: 15px 20px;
}

